/* global window */

/**
 * @customfunction
 */
export async function answer(question: string[][][], invocation: CustomFunctions.Invocation): Promise<string> {
  const fullQuestion = question.flat().flat().join("\n");
  return await window.responder
    .response(fullQuestion)
    .then(resp => resp.explanation)
    .catch((e) => Promise.reject(new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidReference, e)));
}


/**
 * @customfunction
 */
export async function answer_yes_no(question: string[][][], invocation: CustomFunctions.Invocation): Promise<string> {
  const fullQuestion = question.flat().flat().join("\n");
  return await window.responder
    .response(fullQuestion)
    .then(resp => resp.answer)
    .catch((e) => Promise.reject(new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidReference, e)));
}

CustomFunctions.associate("ANSWER", answer);
CustomFunctions.associate("ANSWER_YES_NO", answer_yes_no);